.Select {
  margin-bottom: 15px;
}

.Select label {
  display: block;
  margin: 0 0 3px;
  padding: 0;
  font-weight: 900;
}

.Select select {
  display: block;
  margin: 0 0 5px;
  outline: none;
  border: 1px solid #bebebe;
  width: 100%;
  height: 29px;
  box-sizing: border-box;
  transition: all 300ms ease-in-out;
}
